import { Component, OnInit, ViewChild } from '@angular/core';
import { DestroyService } from '@shared/services/destroy.service';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable, takeUntil } from 'rxjs';
import { environment as env } from '@environments/environment';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ClientUser } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { DeleteAccountDialogV2Component } from '../delete-account-dialog-v2/delete-account-dialog-v2.component';
import { APP_ROUTE, VALIDATOR } from '@core/constants';
import { ImageService } from '@core/services/image.service';
import { DEFAULT_EMAIL_PLACEHOLDER, EDIT_DIALOG_STATIC_DATA } from '@feature/my-profile/my-profile.constant';
import { MatInput } from '@angular/material/input';
import { EditProfileInfoDialogComponent } from '../edit-profile-info-dialog/edit-profile-info-dialog.component';
import { ProfileInfoField } from '@core/enums/profile.enum';
import { DeleteBusinessDialogComponent } from '@feature/my-profile/components/delete-business-dialog/delete-business-dialog.component';

@Component({
    selector: 'findqo-individual-account-v2',
    templateUrl: './individual-account-v2.component.html',
    styleUrls: ['./individual-account-v2.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class IndividualAccountV2Component implements OnInit {
	constructor(
		private myProfileFacade: MyProfileFacade,
		private destroyService: DestroyService,
		private fb: UntypedFormBuilder,
		private dialog: MatDialog,
		public viewport: ViewportService,
		private imageService: ImageService
	) {
		this.user$ = myProfileFacade.getUser$();
	}

	@ViewChild('phoneNumber') phoneNumber?: MatInput;

	userDetails: ClientUser;
	checkEmailPreference: boolean;
	selectedFile: File | null = null;
	defaultUserImg: string = env.defaultAvatarImg;
	joinedDate: string;
	isPhotoUploading = false;
	defaultEmailPlaceholder = DEFAULT_EMAIL_PLACEHOLDER;
	readonly buttonType = ButtonType;
	readonly appRoutes = APP_ROUTE;
	readonly profileInfoField = ProfileInfoField;
	user$: Observable<ClientUser>;

	form = this.fb.group({
		name: ['', [Validators.required, VALIDATOR.pattern.name]],
		phoneCode: ['+353', Validators.required],
		phone: [undefined, [Validators.required, Validators.pattern('^[0-9]*$')]],
	});

	mouseClick(): void {
		setTimeout(() => {
			this.phoneNumber?.focus();
		}, 200);
	}

	showUserDetails(): void {
		this.user$.pipe(takeUntil(this.destroyService)).subscribe((user: ClientUser) => {
			if (user) {
				this.userDetails = user;
				this.form.get('name').setValue(user?.name || '');
				this.form.get('phone').setValue(user?.phone || '');
				this.checkEmailPreference = user?.preference?.marketingEmailAlerts === '1';
			}
		});
	}

	updateAvatar(): void {
		this.myProfileFacade.getUserAvatar();
	}

	onFileSelect(event: any): void {
		if (event.target.files.length === 0) {
			return;
		}

		this.selectedFile = <File>event.target.files[0];
		this.onUpload();
	}

	onUpload(): void {
		if (this.selectedFile) {
			this.isPhotoUploading = true;

			this.imageService.getCompressedImage(this.selectedFile, 400).then((file) => {
				this.selectedFile = file;

				this.myProfileFacade
					.uploadUserProfilePhoto(this.selectedFile)
					.pipe(takeUntil(this.destroyService))
					.subscribe(() => {
						this.isPhotoUploading = false;
					});
			});
		}
	}

	editProfileInfo(target: string): void {
		const DATA = { ...EDIT_DIALOG_STATIC_DATA[target], ...this.userDetails, target };

		this.dialog
			.open(EditProfileInfoDialogComponent, {
				disableClose: false,
				maxWidth: '375px',
				width: '95%',
				height: 'auto',
				maxHeight: '100%',
				data: DATA,
			})
			.afterClosed()
			.subscribe((value) => {
				if (value && value !== DATA[target]) {
					this.myProfileFacade.updateProfile({
						[target]: value,
					});
				}
			});
	}

	savePhoneNumber(): void {
		this.myProfileFacade.updateProfile({ phoneCode: this.form.get('phoneCode').value, phone: this.form.get('phone').value });
	}

	onSubmit(): void {
		if (this.form.valid) {
			this.myProfileFacade.updateProfile(this.form.value);
			this.form.markAsPristine();
		}
	}

	deleteAccount(): void {
		if (+this.userDetails.hasBusinessProfile) {
			this.openBusinessDeleteDialog();
			return;
		}

		this.dialog.open(DeleteAccountDialogV2Component, {
			width: '375px',
			height: 'auto',
			data: { email: this.userDetails.email, adCount: this.userDetails.liveAds },
			panelClass: 'delete-account-dialog',
			disableClose: true,
		});
	}

	openBusinessDeleteDialog(): void {
		this.dialog.open(DeleteBusinessDialogComponent, {
			width: '95%',
			maxWidth: '500px',
			height: 'auto',
		});
	}

	ngOnInit(): void {
		this.showUserDetails();
	}
}
