import { Component, Input } from '@angular/core';
import { ButtonType } from '@shared/components/button/button.component';
import { SubscriptionOfferDialogComponent } from '../subscription-offer-dialog/subscription-offer-dialog.component';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';

@Component({
    selector: 'findqo-subscription-offer',
    templateUrl: './subscription-offer.component.html',
    styleUrls: ['./subscription-offer.component.scss'],
    standalone: false
})
export class SubscriptionOfferComponent {
	constructor(public dialogRef: MatDialogRef<SubscriptionOfferDialogComponent>, public router: Router) {}

	readonly buttonType = ButtonType;
	@Input() isSubscriptionDialog: boolean;
	subscriptionList = ['n3390', 'n3391', 'n3392'];

	closeSubscriptionDialog(): void {
		this.dialogRef.close();
	}

	viewOptions(): void {
		this.router.navigate([APP_ROUTE.pricing]);
		this.closeSubscriptionDialog();
	}
}
