import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { DeleteAccountOtpV2Component } from '@feature/auth/v2/components/delete-account-otp-v2/delete-account-otp-v2.component';
import { ButtonType } from '@shared/components/button/button.component';
import { AuthType } from '@core/enums';
import { AuthDialogV2Component } from '@feature/auth/v2/auth-dialog-v2.component';
import { AuthFacade } from '@feature/auth/auth.facade';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';

@Component({
    selector: 'findqo-delete-account-dialog-v2',
    templateUrl: './delete-account-dialog-v2.component.html',
    styleUrls: ['./delete-account-dialog-v2.component.scss'],
    standalone: false
})
export class DeleteAccountDialogV2Component implements OnInit {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: any,
		private dialog: MatDialog,
		public dialogRef: MatDialogRef<DeleteAccountOtpV2Component>,
		private authFacade: AuthFacade
	) {}

	readonly buttonType = ButtonType;

	onDismiss(): void {
		this.dialogRef.close(false);
	}

	continue(): void {
		this.authFacade.requestOtpForDeleteAccount();

		this.dialogRef.close();
		this.dialog.open(AuthDialogV2Component, {
			data: { authType: AuthType.DELETE_OTP, email: this.data?.email },
			panelClass: 'auth-dialog',
			disableClose: true,
		});
	}

	ngOnInit(): void {}
}
